@font-face {
  font-family: UniSansHeavyCaps;
  font-display: swap;
  src: url("../fonts/UniSansHeavyCaps.woff2") format("woff2"), url("../fonts/UniSansHeavyCaps.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  color: #000;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  min-height: 100vh;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;
}

.header {
  height: 90px;
  background-color: #14c0d4;
  color: #fff;
}
.header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 100%;
}
.header__centerelement {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  width: 200px;
  height: 130px;
  border: 1px solid #000;
  background-color: #14c0d4;
}
.header__phones {
  border: 1px solid #000;
}

.navigation {
  width: 450px;
  padding: 9px 0 18px;
  border: 1px solid #000;
}
.navigation__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: flex-start;
  flex-wrap: wrap;
  height: 100%;
}
.navigation__item {
  align-self: flex-start;
  margin-right: 5px;
  border-radius: 14px;
  background-color: #14c0d4;
}
.navigation__item a {
  text-decoration: underline;
}
.navigation__item:hover {
  background-color: #068292;
}
.navigation__link {
  padding: 5px 22px;
  font-family: Play, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #000;
}
.logo__main {
  font-family: Play, sans-serif;
  font-size: 48px;
  font-weight: 700;
}
.logo__secondary {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 300;
}